<template>
  <glui-select v-model="selected" :disabled="disabled" placeholder="请输入城市名称" :multiple="multiple" :clearable="clearable" filterable remote :remote-method="search" v-on:change="onchange" v-on:clear="clear">
    <glui-option v-for="opt in datas" :key="opt.id" :label="isinner?opt.name:opt.fullname?opt.fullname:opt.name" :value="opt.id">
    </glui-option>
  </glui-select>
</template>
<script>
import httputils from "@/js/httputils";
export default {
  data: function () {
    return {
      datas: [],
      selected: this.value,
      selectdatas: []
    };
  },
  props: {
    props: {
      type: [Object],
      default() {
        return {
          id: "id",
          name: "name"
        };
      }
    },
    defaultitems: {
      default: function () {
        return null;
      },
      type: [Array]
    },
    value: {
      type: [String, Number, Array],
      default: function () {
        return "";
      }
    },
    isinner: {
      type: Boolean,
      default: function () {
        return true;
      }
    },
    isonlydiji: {
      type: Boolean,
      default: function () {
        return false;
      }
    },
    multiple: {
      type: Boolean,
      default: function () {
        return false;
      }
    },
    clearable: {
      type: Boolean,
      default: function () {
        return false;
      }
    },
    disabled: {
      type: Boolean,
      default: function () {
        return false;
      }
    }
  },
  mounted() {
    let that = this;
    if (!that.defaultitems) {
      let keyword = that.selected;
      if (Array.isArray(that.selected)) {
        keyword = that.selected.join(",");
      }
      if (keyword && keyword.length >= 1) {
        that.search(keyword, 1);
      }
    }
  },
  watch: {
    value: function (val, old) {
      let that = this;
      if (val != old) {
        let keyword = val;
        if (Array.isArray(val)) {
          keyword = val.join(",");
        }
        if (keyword && keyword.length >= 1) {
          that.search(keyword, 1);
          that.selected = val;
        }
      }
    },
    defaultitems: function (val, old) {
      let that = this;
            if (val != old && val.length >= 1) {
        let datas = [];
        let selected = that.multiple ? [] : "";
        val &&
          val.forEach(item => {
            if (that.multiple) {
              selected.push(item[that.props.id]);
            } else {
              selected = item[that.props.id];
            }
            datas.push({
              id: item[that.props.id],
              name: item[that.props.name]
            });
          });
        that.datas = datas;
        that.selected = selected;
      }
    }
  },

  methods: {
    search: function (keyword, type) {
      var that = this;
      if (!keyword || keyword.length <= 0) {
        return;
      }
      httputils
        .get("/travel/supergoproduct/Common/GetCity", {
          key: keyword,
          isinner: that.isinner,
          type: type,
          isonlydiji: that.isonlydiji
        })
        .then(function (resp) {
          if (resp) {
            that.datas = resp;
          } else {
            that.datas = [];
          }
        })
        .catch(err => {
          that.$message({
            type: "error",
            message: err
          });
        });
    },
    onchange: function (val) {
      let that = this;
      this.$emit("input", val);
      let selected = that.datas.concat(that.selectdatas).filter(item => {
        if (that.multiple === true) {
          return val.findIndex(v => v == item.id) >= 0;
        } else {
          return item && item.id == val;
        }
      });
      that.selectdatas = selected;
      that.$emit(
        "change",
        val,
        selected && selected.length == 1 ? selected[0] : selected
      );
    },
    clear: function () {
      this.datas = this.defaultitems;
    }
  }
};
</script>




