<template>
    <glui-select v-model="selected" placeholder="请输入供应商名称" :disabled="disabled" :clearable="clearable" filterable remote :remote-method="search" v-on:change="onchange">
        <glui-option v-for="item in datas" :key="item.SupplierId" :label="item.SupplierName" :value="item.SupplierId">
        </glui-option>
    </glui-select>
</template>
<script>
import httputils from "@/js/httputils"

export default {
    data: function () {
        return {
            datas: [],
            selected: this.value
        };
    },
    props: {
        defaultitem: {
            default: function () {
                return null;
            },
            type: Object
        },
        value: {
            type: [String, Number, Array],
            default: function () {
                return "";
            }
        },
        belong: {
            type: [String, Number],
            default: function () {
                return 1;
            }
        },
        clearable: {
            type: Boolean,
            default: function () {
                return false;
            }
        },
        disabled: {
            type: Boolean,
            default: function () {
                return false;
            }
        }
    },
    watch: {
        defaultitem: function (val) {
            if (this.defaultitem) {
                this.datas = [this.defaultitem];
                this.selected = this.defaultitem.SupplierId;
            }
        }
    },
    mounted: function () {
        if (this.defaultitem) {
            this.datas = [this.defaultitem];
            this.selected = this.defaultitem.SupplierId;
        } else {
            var key = this.value;
            if (key && key.length >= 1) {
                this.search(key, 1);
            }
        }
    },
    methods: {
        search: function (keyword) {
            var that = this;
            if (!keyword || keyword.length <= 0) {
                return;
            }
            httputils.get(
                "/travel/supergoproduct/ProductSupplier/QueryProductSupplierList",
                {
                    keyword: keyword,
                    belong: that.belong
                }
            )
                .then(function (resp) {
                    if (resp) {
                        that.datas = resp;
                    } else {
                        that.datas = [];
                    }
                }).catch(err => {
                    that.$message({
                        type: 'error',
                        message: err
                    });
                });
        },
        onchange: function (val) {
            this.$emit("input", val);
            var item = null;
            this.datas.forEach(function (opt) {
                if (opt && opt.SupplierId == val) {
                    item = opt;
                    return;
                }
            });
            this.$emit("change", val, item);
        }
    }
}
</script>



