import Vue from "vue";
import httputils from "@/js/httputils";
import singleresource from "./components/singleresource";
import scenicresource from "./components/scenicresource";
import addspecialresource from "./components/addspecialresource";

import "./index.css";

import { resourceTypes, resourcePlats } from "./config/index";

let vue = new Vue({
  el: "#container",
  components: {
    singleresource,
    scenicresource,
    addspecialresource
  },
  data: function() {
    return {
      belong: $("#productBelong").val(),
      productId: $("#productid").val(),
      tripsTotal: $("#tripsTotal").val(),
      containvisa: $("#ContainVisa").val(),
      travelDays: Array.from(
        {
          length: $("#productmaxday").val()
        },
        (v, k) => {
          return k + 1;
        }
      ),
      travelVersions: JSON.parse($("#productversions").val()),
      baseInfo: {
        singleList: [],
        specialList: [],
        scenicList: []
      },
      single: {
        dialogShow: false,
        useTravels: [],
        resourcetypes: resourceTypes,
        platsources: resourcePlats,
        searchForm: {
          pageSize: 10,
          total: 0,
          PageIndex: 1,
          SupplierId: "",
          CityId: "",
          ResourceType: "",
          PlatSource: null,
          ResourceName: null,
          ResourceId: ""
        },
        searchResult: [],
        selectionDatas: []
      },
      rules: {
        "baseInfo.singleList": [
          {
            required: true,
            message: "此处不可为空",
            trigger: "change"
          }
        ],
        "baseInfo.specialList": [
          {
            required: true,
            message: "此处不可为空",
            trigger: "change"
          }
        ]
      }
    };
  },
  mounted: function() {
    this.initDatas();
  },
  methods: {
    initDatas: function() {
      let that = this;
      let loading = that.$loading({
        lock: true
      });
      httputils
        .get("/travel/supergoproduct/AdditionItem/GetAdditionalResource", {
          productId: that.productId
        })
        .then(function(resp) {
          loading.close();
          if (resp) {
            that.baseInfo = Object.assign(
              that.baseInfo,
              minux.checkBe({
                singleList: resp.Single,
                specialList: resp.Special,
                scenicList: resp.Scenic
              })
            );
            that.baseInfo.singleList &&
              that.baseInfo.singleList.forEach(function(item) {
                if (!item.StartDay) {
                  item.StartDay = "";
                }
                var version = that.travelVersions.find(
                  v => v.value == item.UseTravels
                );
                if (!version) {
                  item.travelDays = that.travelDays;
                } else {
                  item.travelDays = version.day;
                }
              });
          }
        })
        .catch(err => {
          that.$message({
            type: "error",
            message: err
          });
          loading.close();
        });
    },
    saveDatas: function() {
      var that = this;
      this.$refs["baseInfo"].validate(function(valid) {
        if (valid) {
          let isMore = false;
          if (
            that.baseInfo.singleList &&
            that.baseInfo.singleList.length >= 1
          ) {
            that.baseInfo.singleList.forEach(item => {
              let lstSort = that.baseInfo.singleList.filter(
                single => single.Sort == item.Sort
              );
              if (lstSort && lstSort.length >= 2) {
                isMore = true;
                return;
              }
            });
          }
          if (isMore === true&&that.belong==2) {
            that.$confirm("单品资源售卖顺序不能重复", "保存失败", {
              confirmButtonText: "确定",
              type: "warning",
              center: true,
              showCancelButton: false
            });
            return;
          }
          let loading = that.$loading({
            lock: true
          });
          httputils
            .post(
              "/travel/supergoproduct/AdditionItem/SaveAdditionalResource",
              {
                ProductId: that.productId,
                Single: that.baseInfo.singleList,
                Special: that.baseInfo.specialList,
                Scenic: that.baseInfo.scenicList || []
              }
            )
            .then(resp => {
              loading.close();
              if (resp && resp.IsSuccess) {
                that.$message({
                  message: "附加资源保存成功！",
                  type: "success"
                });
              } else {
                that.$confirm(resp.ErrorMsg, "保存失败", {
                  confirmButtonText: "确定",
                  type: "warning",
                  center: true,
                  showCancelButton: false
                });
              }
            })
            .catch(err => {
              that.$message({
                type: "error",
                message: err
              });
              loading && loading.close();
            });
        } else {
          that.$confirm("请检查输入内容是否完整", "保存失败", {
            confirmButtonText: "确定",
            type: "warning",
            center: true,
            showCancelButton: false
          });
        }
      });
    },
    removespecial: function(index) {
      this.$confirm("确认删除此特殊附加资源吗？", "确认操作", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          if (this.baseInfo.specialList) {
            this.baseInfo.specialList.splice(index, 1);
          }
        })
        .catch(() => {});
    },
    removesingle: function(index) {
      this.$confirm("确认删除此单产品资源吗？", "确认操作", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          if (this.baseInfo.singleList) {
            this.baseInfo.singleList.splice(index, 1);
          }
        })
        .catch(() => {});
    },
    removescenic: function(index) {
      this.$confirm("确认删除此景区资源吗？", "确认操作", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          if (this.baseInfo.scenicList) {
            this.baseInfo.scenicList.splice(index, 1);
          }
        })
        .catch(() => {});
    },
    changeSwitch: function(item, index) {
      var that = this;
      var message = "";
      if (item.IsMustChoose == 1) {
        message = "确认要必选此单品吗？";
      } else {
        message = "确认要取消必选此单品吗？";
      }
      that
        .$confirm(message, "确认操作", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true
        })
        .then(() => {})
        .catch(() => {
          item.IsMustChoose = item.IsMustChoose == 1 ? 0 : 1;
        });
    },
    changeDay: function(item) {
      console.log(item);
    },
    opendialog: function(ref) {
      this.$refs[ref].openDialog();
    }
  }
});

$(function() {
  $("#saveProductInfo").click(function() {
    vue.saveDatas();
  });
});



