<template>
    <glui-dialog title="新增特殊附加资源" :visible.sync="dialogShow" width="90%" v-on:open="openDialog">
        <glui-form :model="form" :rules="rules" label-width="100px">
            <glui-form-item label="资源名称" prop="ResourceName" required>
                <glui-input v-model="form.ResourceName" placeholder="请输入资源名称" :maxlength="50">
                </glui-input>
            </glui-form-item>
            <glui-form-item label="供应商" prop="SupplierId" required>
                <dropdownsupplier v-model="form.SupplierId" :belong="1">
                </dropdownsupplier>
            </glui-form-item>
            <glui-form-item label=" ">
                <button class="btn btn-default" type="button" id="js_query" v-on:click="saveDatas">
                    <i class="bico bico-search"></i>保&nbsp;存
                </button>
            </glui-form-item>
        </glui-form>
    </glui-dialog>
</template>
<script>
import httputils from "@/js/httputils";
import dropdownsupplier from "@/components/dropdownsupplier";
export default {
    components: { dropdownsupplier },
    data: function () {
        return {
            dialogShow: false,
            form: {
                ResourceName: "",
                SupplierId: ""
            },
            rules: {
                SupplierId: [
                    { required: true, message: "此处不可为空", trigger: "change" }
                ],
                ResourceName: [
                    { required: true, message: "此处不可为空", trigger: "blur" }
                ]
            }
        };
    },
    props: {
        productid: {
            type: [Number, String],
            default: function () {
                return 0;
            }
        },
        onclose: {
            type: Function,
            default: function () {
                return function () { };
            }
        }
    },
    mounted: function () { },
    methods: {
        openDialog: function () {
            this.dialogShow = true;
        },
        saveDatas: function () {
            let that = this;
            let loading = that.$loading({ lock: true });
            httputils.post('/travel/supergoproduct/AdditionItem/CreateSpecialResource', {
                SupplierId: that.form.SupplierId,
                ResourceName: that.form.ResourceName,
                ProductId: that.productid
            }).then((resp) => {
                loading.close();
                if (resp && resp.IsSuccess) {
                    that.dialogShow = false;
                    that.onclose();
                } else {
                    that.$alert(resp.ErrorMsg, "保存失败", {
                        dangerouslyUseHTMLString: true,
                        type: "warning",
                        center: true
                    });
                }
            }).catch((err) => {
                that.$message({
                    type: 'error',
                    message: err
                });
            });
        }
    }
}
</script>




