<template>
    <glui-dialog title="新增单产品资源" :visible.sync="dialogShow" width="90%" v-on:open="openDialog">
        <glui-form :model="searchForm" label-width="100px">
            <glui-row>
                <glui-col :span="8">
                    <glui-form-item label="资源ID" width="80" style="margin-bottom:0;">
                        <glui-input v-model="searchForm.ResourceId" auto-complete="off"></glui-input>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="资源名称" style="margin-bottom:0;">
                        <glui-input v-model="searchForm.ResourceName" auto-complete="off"></glui-input>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="资源分类" width="120" style="margin-bottom:0;">
                        <glui-select v-model="searchForm.ResourceType" clearable placeholder="请选择">
                            <glui-option v-for="item in resourcetypes" :key="item.id" :label="item.name" :value="item.id">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
            </glui-row>
            <glui-row>
                <glui-col :span="8">
                    <glui-form-item label="所在城市" width="80" style="margin-bottom:0;">
                        <dropdowncity v-model="searchForm.CityId" :isinner="false" clearable></dropdowncity>
                    </glui-form-item>
                </glui-col>
                <!--  <glui-col :span="8" v-if="belong==1">
                    <glui-form-item label="资源来源" width="80" style="margin-bottom:0;">
                        <glui-select v-model="searchForm.ResourceFrom" clearable placeholder="请选择">
                            <glui-option v-for="item in ResourceFroms" :key="item.id" :label="item.name" :value="item.id">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>-->
                <glui-col :span="8" v-if="belong==1">
                    <glui-form-item label="供应商" style="margin-bottom:0;">
                        <dropdownsupplier v-model="searchForm.SupplierId" :belong="belong" clearable>
                        </dropdownsupplier>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <div class="pull-right">
                        <button class="btn btn-default" type="button" id="js_query" v-on:click="searchSingle">
                            <i class="bico bico-search"></i>查&nbsp;询
                        </button>
                        <button class="btn btn-warning" type="button" id="js_add" v-on:click="confirmSelection">
                            <i class="bico bico-add-double"></i>确认选择
                        </button>
                    </div>
                </glui-col>
            </glui-row>
            <glui-row v-show="belong==1&&tripstotal>=2">
                <glui-col :span="13">

                    <glui-form-item label="适用行程版本" style="margin-bottom:0;">
                        <glui-select v-model="travels" multiple placeholder="请仔细行程版本，注意确认后无法更改哦">
                            <glui-option v-for="item in versions" :key="item.value" :label="item.text" :value="item.value">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="9">
                    <glui-form-item label-width="0px" style="margin-bottom:0;margin-left: 10px;">
                        <span style="color:red;">请尽量选择同一家供应商提供的签证资源（支持多选）</span>
                    </glui-form-item>
                </glui-col>
            </glui-row>
        </glui-form>
        <glui-table ref="singleTable" :data="searchResult" v-on:selection-change="selectionChange">
            <glui-table-column prop="ResourceId" label="资源ID" width="80">
            </glui-table-column>
            <glui-table-column prop="ResourceTypeName" label="资源品类" width="120">
            </glui-table-column>
            <glui-table-column prop="ResourceName" label="单品资源名称">
            </glui-table-column>
            <glui-table-column label="座位数" width="100" v-if="belong==2">
                <template slot-scope="scope">
                    {{scope.row.LoadLimit>=1?scope.row.LoadLimit:"--"}}
                    <span v-if="scope.row.LoadLimit>=1" style="color:red;">（含司机）</span>
                </template>
            </glui-table-column>
            <glui-table-column prop="SupplierName" label="供应商" v-if="belong==1">
            </glui-table-column>
            <glui-table-column prop="BelongCity" label="所在城市" width="80">
            </glui-table-column>
            <glui-table-column prop="PlatSourceName" label="来源" width="80" v-if="belong==1">
            </glui-table-column>
            <glui-table-column label="团期价格" width="150" v-if="belong==1">
                <div slot-scope="scope">
                    <a :href="scope.row.Url" target="_blank">
                        团期价格
                    </a>
                </div>
            </glui-table-column>
            <glui-table-column label="选择" type="selection">
            </glui-table-column>
        </glui-table>
        <glui-pagination v-on:size-change="sizeChange" v-on:current-change="pageChange" :page-sizes="[10,20]" layout="total, sizes, prev, pager, next, jumper" style="text-align:center;margin-top:14px;" :page-size="searchForm.pageSize" :total="searchForm.total">
        </glui-pagination>
    </glui-dialog>
</template>
<script>
import httputils from "@/js/httputils";
import { resourceTypes, resourcePlats } from "../config/index";
import dropdowncity from "@/components/dropdowncity";
import dropdownsupplier from "@/components/dropdownsupplier";
export default {
    components: { dropdowncity, dropdownsupplier },
    data: function () {
        return {
            dialogShow: false,
            travels: [],
            ResourceFroms: resourcePlats,
            searchForm: {
                pageSize: 10,
                total: 0,
                PageIndex: 1,
                SupplierId: "",
                CityId: "",
                ResourceType: "",
                ResourceFrom: null,
                ResourceName: null,
                ResourceId: "",
                belong: this.belong
            },
            searchResult: [],
            selectionDatas: [],
            containVisa1: 0
        };
    },
    props: {
        tripstotal: {
            type: [String, Number],
            default: function () {
                return 1;
            }
        },
        versions: {
            type: Array,
            default: function () {
                return [];
            }
        },
        belong: {
            type: [String, Number],
            default: function () {
                return 1;
            }
        },
        containvisa: {
            type: [String, Number],
            default: function () {
                return 0;
            }
        },
        selection: {
            type: Array,
            default: function () {
                return [];
            }
        }
    },
    computed: {
        resourcetypes: function (val) {
            if (this.belong == 1) {
                if (this.containvisa == 0) {
                    return resourceTypes
                }
                else {
                    return resourceTypes.filter(v => { return v !== 5 });
                }

            } else {
                return resourceTypes.filter(v => { return v === 1 || v === 4 || v === 3 });
            }
        }
    },
    mounted: function () { },
    methods: {
        sizeChange: function (val) {
            this.searchForm.pageSize = val;
            this.searchSingle();
        },
        pageChange: function (val) {
            this.searchForm.PageIndex = val;
            this.searchSingle();
        },
        selectionChange: function (val) {
            this.selectionDatas = val || [];
        },
        initSelectionRow: function () {
            //var that = this;
            //that.$nextTick(function() {
            //  if (!that.selection || that.selection.length <= 0) {
            //    that.$refs.singleTable.clearSelection();
            //  } else {
            //    that.searchResult.forEach(function(item, index) {
            //      var exists = that.selection.find(function(v) {
            //        return v.ResourceId == item.ResourceId;
            //      });
            //      if (exists) {
            //        that.$refs.singleTable.toggleRowSelection(item);
            //      } else {
            //        that.$refs.singleTable.toggleRowSelection(item, false);
            //      }
            //    });
            //  }
            //});
        },
        confirmSelection: function () {
            var that = this;
            if (!that.selectionDatas || that.selectionDatas.length <= 0) {
                return;
            }
            if (that.belong == 1 && that.tripstotal >= 2 && (!that.travels || that.travels.length < 1)) {
                that.$alert("请选择资源所属行程版本！", "选择失败", {
                    dangerouslyUseHTMLString: true,
                    type: "warning",
                    center: true
                });
                return;
            }
            //var datas = [];
            //that.selectionDatas.forEach(function (item) {
            //    var exists = that.selection.find(function (old) {
            //        return old.ResourceId == item.ResourceId;
            //    });
            //    if (!exists) {
            //        datas.push(item);
            //    }
            //});
            //datas = (that.selection || []).concat(datas);
            var newDatas = [];
            that.selectionDatas.forEach(function (item) {
                if (that.belong == 1 && that.tripstotal >= 2) {
                    that.travels.forEach(function (travel) {
                        var exists = that.selection.find(function (old) {
                            return old.ResourceId == item.ResourceId && travel == old.UseTravels;
                        });
                        if (!exists) {
                            var version = that.versions.find(v => v.value == travel);
                            if (!version) {
                                version = {
                                    value: travel,
                                    day: 1,
                                    text: ""
                                };
                            }
                            newDatas.push({
                                Id: item.Id,
                                ResourceId: item.ResourceId,
                                ResourceName: item.ResourceName,
                                BelongCity: item.BelongCity,
                                ResourceType: item.ResourceType,
                                ResourceTypeName: item.ResourceTypeName,
                                IsMustChoose: item.IsMustChoose,
                                IsGift: item.IsGift,
                                Url: item.Url,
                                LoadLimit: item.LoadLimit,
                                StartDay: item.StartDay,
                                EndDay: item.EndDay,
                                SupplierName: item.SupplierName,
                                PlatSource: item.PlatSource,
                                PlatSourceName: item.PlatSourceName,
                                UseVersions: version.text,
                                UseTravels: travel,
                                travelDays: version.day
                            });
                        }
                    });
                } else {
                    item.UseVersions = item.UseTravels = "";
                    item.travelDays = 1;

                    var exists = that.selection.find(function (old) {
                        return old.ResourceId == item.ResourceId;
                    });
                    if (!exists) {
                        newDatas.push(item);
                    }
                }
            });
            //PM要求购物券默认赠送且不可更改
            var datas = (that.selection || []).concat(newDatas);
            datas.forEach(function (item) {
                if (that.belong == 1 && item.ResourceType == 3) {
                    item.IsGift = 1;
                }
                if (!item.StartDay) {
                    item.StartDay = "";
                }
            });
            this.$emit("update:selection", datas);
            that.dialogShow = false;
        },
        openDialog: function () {
            this.dialogShow = true;
            this.searchForm.PageIndex = 1;
            this.searchSingle();
        },
        searchSingle: function () {
            let that = this;
            let loading = that.$loading({ lock: true });
            httputils
                .get("/travel/supergoproduct/AdditionItem/GetSingleProductList", that.searchForm)
                .then(resp => {
                    if (resp) {
                        that.searchResult = resp.SingleList || [];
                        that.searchForm = Object.assign(that.searchForm, {
                            total: resp.TotalCount
                        });
                        that.initSelectionRow();
                        loading.close();
                    }
                }).catch(err => {
                    that.$message({
                        type: 'error',
                        message: err
                    });
                    loading.close();
                });
        }
    }
}
</script>




