<template>
    <glui-dialog v-loading='scenicloading' title="新增景区资源" :visible.sync="dialogShow" width="90%" v-on:open="openDialog">
        <glui-form :model="searchForm" label-width="100px">
            <glui-row>
                <glui-col :span="8">
                    <glui-form-item label="所在城市">
                        <dropdowncity v-model="searchForm.CityId" :isinner="false" clearable></dropdowncity>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="景区名称">
                        <glui-select v-model="searchForm.SceneryId" filterable clearable placeholder="请选择">
                            <glui-option v-for="item in scenicsources" :key="item.ResourceId" :label="item.ResourceName" :value="item.ResourceId">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item>
                        <div class="pull-right">
                            <button class="btn btn-default" type="button" id="js_query" v-on:click="searchScenic">
                                <i class="bico bico-search"></i>查&nbsp;询
                            </button>
                            <button class="btn btn-warning" type="button" id="js_add" v-on:click="confirmSelection">
                                <i class="bico bico-add-double"></i>确认选择
                            </button>
                        </div>
                    </glui-form-item>
                </glui-col>
            </glui-row>
        </glui-form>
        <glui-table :data="searchResult" row-key='ResourceId' :expand-row-keys="expandkeys" v-on:expand-change="expandChange">
            <glui-table-column type="expand">
                <template slot-scope="scenic">
                    <glui-table label-position="left" :ref="'singleTable'+scenic.row.ResourceId" inline v-loading="loadingPolicy" :data="scenic.row.policylist" v-on:selection-change="val=>scenic.row.selection=val">
                        <glui-table-column type="selection" width="55">
                        </glui-table-column>
                        <glui-table-column label="价格策略ID" width="100" prop="PolicyId">
                        </glui-table-column>
                        <glui-table-column prop="PolicyName" label="价格策略名称">
                        </glui-table-column>
                        <glui-table-column prop="ForCrowd" width="80" label="针对人群">
                        </glui-table-column>
                        <glui-table-column label="策略日期">
                            <template slot-scope="policy">
                                {{policy.row.StartTime}}至{{policy.row.EndTime}}
                            </template>
                        </glui-table-column>
                        <glui-table-column prop="TicketMode" label="取票方式">
                        </glui-table-column>
                    </glui-table>
                </template>
            </glui-table-column>
            <glui-table-column prop="ResourceId" label="景点编号" width="80">
            </glui-table-column>
            <glui-table-column prop="ResourceName" label="景区名称">
            </glui-table-column>
            <glui-table-column prop="ProvinceName" label="所在省份">
            </glui-table-column>
            <glui-table-column prop="CityName" label="所在城市">
            </glui-table-column>
            <glui-table-column prop="GrageName" label="等级">
            </glui-table-column>
        </glui-table>
        <glui-pagination v-on:size-change="sizeChange" v-on:current-change="pageChange" :page-sizes="[10,20]" layout="total, sizes, prev, pager, next, jumper" style="text-align:center;margin-top:14px;" :page-size="searchForm.pageSize" :total="searchForm.total">
        </glui-pagination>
    </glui-dialog>
</template>
<script>
import httputils from "@/js/httputils";
import dropdowncity from "@/components/dropdowncity";
export default {
    components: { dropdowncity },
    data: function () {
        return {
            expandkeys: [],
            scenicloading: false,
            loadingPolicy: false,
            dialogShow: false,
            scenicsources: [],
            searchForm: {
                total: 0,
                pageSize: 10,
                PageIndex: 1,
                CityId: "",
                SceneryId: "",
                BelongTo: this.belong
            },
            searchResult: [],
            selectionDatas: []
        };
    },
    props: {
        belong: {
            type: [String, Number],
            default: function () {
                return 1;
            }
        },
        selection: {
            type: Array,
            default: function () {
                return [];
            }
        }
    },
    mounted: function () {
        if (this.belong == 2) {
            this.initScenics();
        }
    },
    methods: {
        initScenics: function () {
            var that = this;
            httputils
                .get("/travel/supergoproduct/AdditionItem/GetSceneryIdNameList", {
                    belong: that.belong
                })
                .then(function (result) {
                    if (result) {
                        that.scenicsources = result.IdNameList || [];
                    }
                }).catch(err => {
                    that.$message({
                        type: 'error',
                        message: err
                    })
                });
        },
        sizeChange: function (val) {
            this.searchForm.pageSize = val;
            this.searchScenic();
        },
        pageChange: function (val) {
            this.searchForm.PageIndex = val;
            this.searchScenic();
        },
        expandChange: function (row, expandedRows) {
            var that = this;
            if (!row.policylist || row.policylist.length <= 0) {
                that.loadingPolicy = true;
                httputils
                    .get("/travel/supergoproduct/AdditionItem/SceneryTicketPrice", {
                        SceneryId: row.ResourceId,
                        BelongTo: that.belong
                    })
                    .then(function (result) {
                        if (result) {
                            row.policylist = result.TicketPriceList || [];
                            setTimeout(function () {
                                that.initSelectionRow(row);
                            }, 200);
                        }
                        that.loadingPolicy = false;
                    }).catch(err => {
                        that.$message({
                            type: 'error',
                            message: err
                        })
                    });
            } else {
                that.initSelectionRow(row);
            }
        },
        selectionChange: function (v, val) {
            this.selectionDatas = val || [];
        },
        initSelectionRow: function (row) {
            var that = this;
            that.$nextTick(function () {
                var currentPolicy = that.$refs["singleTable" + row.ResourceId];
                if (!that.selection || that.selection.length <= 0) {
                    currentPolicy && currentPolicy.clearSelection();
                } else {
                    row.policylist &&
                        row.policylist.forEach(function (item, index) {
                            var exists = that.selection.find(function (v) {
                                return v.PolicyId == item.PolicyId;
                            });
                            if (exists) {
                                currentPolicy && currentPolicy.toggleRowSelection(item);
                            } else {
                                currentPolicy && currentPolicy.toggleRowSelection(item, false);
                            }
                        });
                }
            });
        },
        confirmSelection: function () {
            var that = this;
            var datas = [];
            that.searchResult &&
                that.searchResult.forEach(function (resource) {
                    if (resource.selection && resource.selection.length >= 1) {
                        resource.selection.forEach(function (policy) {
                            var exists = that.selection.find(function (old) {
                                return old.PolicyId == policy.PolicyId;
                            });
                            if (!exists) {
                                datas.push({
                                    PolicyId: policy.PolicyId,
                                    PolicyName: policy.PolicyName,
                                    ForCrowd: policy.ForCrowd,
                                    StartTime: policy.StartTime,
                                    EndTime: policy.EndTime,
                                    PayMethod: policy.PayMethod,
                                    DataFlag: 1,
                                    ResourceId: resource.ResourceId,
                                    ResourceName: resource.ResourceName,
                                    ResourceGrade: resource.ResourceGrade,
                                    TicketMode: policy.TicketMode,
                                    TicketId: policy.TicketId,
                                    ResourceGrade: resource.GrageId,
                                    ResourceGradeName: resource.GrageName
                                });
                            }
                        });
                    }
                });
            this.$emit("update:selection", (that.selection || []).concat(datas));
            that.dialogShow = false;
        },
        openDialog: function () {
            this.dialogShow = true;
            this.searchForm.PageIndex = 1;
            this.searchScenic();
        },
        searchScenic: function () {
            var that = this;
            that.scenicloading = true;
            httputils
                .get("/travel/supergoproduct/AdditionItem/SceneryResourceList", that.searchForm)
                .then(resp => {
                    if (resp) {
                        that.searchResult = resp.SceneryResourceList || [];
                        that.searchForm = Object.assign(that.searchForm, {
                            total: resp.TotalCount
                        });
                        that.scenicloading = false;
                    }
                }).catch(err => {
                    that.$message({
                        type: 'error',
                        message: err
                    })
                });
        }
    }
}
</script>



